.head {
    position: relative;
    background-color: #FF3618;
    height: 80vh;
    max-width: 100%;
}


.video-container {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    bottom: $column-width * 1;
}

.video {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.title-bloc {
    margin-bottom: $column-width * 1;
    z-index: 1000;
}
.big-title {
    font-size: 5vw;
    font-family: 'Helvetica Neu';
    margin-bottom: 30px;
    font-weight: 800;
    
}

@media (max-width: $bp-mobile) {
    .video-container {
        bottom: $column-width * 1;
    }
    .title-bloc {
        margin-bottom: $column-width * 3;
    }
    .big-title {
        font-size: 10vw;
        
    }
}



.op0 {
    opacity: 0;
    transition: ease .5s;
}

.op100 {
    opacity: 1;
    transition: ease .5s;
    animation-delay: 500;
}

.op50 {
    opacity: 0.5;
    transition: ease .5s;
    animation-delay: 500;
}