.tile {
    position: relative;
    height: $column-width * 8.75;
}


.tileimg {
    object-fit: cover;
    width: 100%;
    max-height: 100%;
}

.inner-bottom {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    justify-content: flex-end;
    align-items: center;
}

.inner-title {
    font-family: 'Helvetica Neu';
    font-weight: 800;
    font-size: 3vw;
}

.full {
    transition: ease .5s;
}

@media (max-width: $bp-mobile) {
    .full .tile {
        height: $column-width * 28.75;
    }

    .mid .tile {
        height: $column-width * 13.75;
    }

    .inner-title {
        font-family: 'Helvetica Neu';
        font-weight: 800;
        font-size: 10vw;
    }
}

.bloc {
    opacity: 0;
    transition: ease .5s;
}

.visible {
    opacity: 1;
    transition: ease .5s;
}