

@font-face {
    font-family: 'Helvetica Neu';
    src: url('../fonts/Helvetica73BoldExtended.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}




h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

.text-xxl {
    font-weight: 500;
    font-size: 7rem;
    line-height: 9.1rem;
    letter-spacing: -0.3rem;
}

.text-xl {
    font-weight: 500;
    font-size: 5rem;
    line-height: 7.5rem;
}

.text-l {
    font-weight: 500;
    font-size: 2.8rem;
    line-height: 4.2rem;
}

.text-m {
    font-weight: 500;
    font-size: 2.2rem;
    line-height: 3.3rem;
}

.text-s {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.4rem;
}

.text-xs {
    font-weight: 400;
    font-size: 1.3rem;
    line-height: 2rem;
}

.text-overline {
    font-weight: 600;
    font-size: 1rem;
    line-height: 2rem;
    letter-spacing: 1rem;
    text-transform: uppercase;
}

.light {
    color: $white;
}

.dark {
    color: $mineshaft;
}

.align-left {
    text-align: left;
}

.align-center {
    text-align: center;
}

.align-right {
    text-align: right;
}

.fw-reg {
    font-weight: 400;
}

.fw-bold {
    font-weight: 600;
}

@media (max-width: $bp-mobile) {
    .text-xxl {
    font-weight: 600;
    font-size: 4rem;
    line-height:5rem;
    letter-spacing: -0.3rem;
}
    
}

